.wrapper{min-height:calc(100vh - 60px);background:transparent;padding:16px;position:relative;top:60px !important}.wrapper h1{color:#000;font-size:7rem;font-weight:900;font-weight:700;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);animation:textAnimation 1s;opacity:1;width:80%;background-image:url(/static/media/chariot1.c34dc035.jpg);background-repeat:repeat;-webkit-background-clip:text;-webkit-text-fill-color:transparent;-webkit-font-smoothing:antialiased}@media only screen and (max-width: 900px){.wrapper{padding:0;background-color:transparent}h1{font-size:3rem !important;width:90%;transition:1s ease-in}}@media only screen and (max-width: 480px){.wrapper{padding:0;background-color:transparent}h1{font-size:2.2rem !important;width:90%;transition:1s ease-in}}@keyframes textAnimation{from{top:40%;opacity:0}to{top:50%;opacity:1}}.chapter-row{width:65% !important;position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);justify-content:center;transition:1s ease-in;background:rgba(55,185,238,0.4);box-shadow:0 6px 12px 0 rgba(31,38,135,0.37);backdrop-filter:blur(11px);-webkit-backdrop-filter:blur(11px);border-radius:30px;border:none !important}@media only screen and (max-width: 900px){.chapter-row{padding:0;background:transparent;width:90% !important;transition:1s ease-in}}@media only screen and (max-width: 480px){.chapter-row{padding:0;background:transparent;width:90% !important;transition:1s ease-in}}.navbar{height:60px !important;position:fixed !important;width:100%;z-index:3000;background:rgba(55,185,238,0.4);box-shadow:0 5px 10px 0 rgba(31,38,135,0.37);backdrop-filter:blur(11px);-webkit-backdrop-filter:blur(11px);border:none !important}.navbar .navbar-brand{color:#111 !important;font-size:1.5rem;font-weight:800}.navbar .nav-link a{color:#111 !important;font-size:1rem;font-weight:500}.navbar .nav-link a:hover{color:#57a2e0}@media only screen and (max-width: 900px){.navbar-collapse{background:#f7f4f4 !important;z-index:2000;width:100% !important}}@media only screen and (max-width: 480px){.navbar-collapse{background:#f7f4f4 !important;z-index:2000;width:100% !important}}.text-box{width:100%;transition:animation 2s ease;animation:skeletonAnimation 1s infinite}@keyframes skeletonAnimation{from{width:50%}to{width:100%}}

